:root {
  --border-color: black;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

header{
  background: dimgray;
  width: 100%;
}
.header__container {
  height: 130px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  margin: 10px 100px;
}
.container--important{
  height: 100%;
  display: flex;
  align-items: center;
}
.container--settings{
  height: 100%;
  display: flex;
  align-items: center;
}
.container__important--canvas-size {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-right: 40px;
  border: 1px solid rgb(168, 168, 168);
  border-radius: 5px;
  padding: 10px;
}
.container__important--canvas-size label{
  color: white;
  font-size: 18px;
}
.container__important--canvas-size input[type=number]{
  padding: 5px;
  background: white;
  border: none;
  outline: none;
  cursor: text;
  color: black;
  width: 70px;
}
.container__important--brush{
  display: flex;
  gap: 10px;
}

.container__input-setting {
  display: flex;
  align-items: center;
  border-radius: 5px;
  padding: 10px;
  margin: 0px 10px;
  background: rgb(168, 168, 168);
}
.container__input-setting p{
  color: white;
}
.container__input-setting input{
  margin-right: 10px;
}

.container{
  background: gray;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.canvas-container{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100px;
}
canvas {
  background: white;
  border: 1px solid black;
}

input {
  background-color: rgb(168, 168, 168);
  font-size: 18px;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
input[type=color], input[type=button] {
  height: 40px;
  box-shadow: 0 0 1px white;
  text-shadow: 0 0 1px black;
  transition: box-shadow .5s;
}
input[type=button]{
  min-width: 60px;
}
input[type=checkbox]{
  width: 20px;
  height: 20px;
}
input[type=color]:hover, input[type=button]:hover{
  box-shadow: 0 0 10px white;
}